html {
  color: #212121;
  background-color: #fff;
  font-family: verdana, arial, sans-serif;
  font-size: 22px;
  line-height: 1.3;
}

turbo-frame {
  display: block;
}

input, textarea, select, button {
  font-size: 100%;
}

a {
  color: #ec005a;
}

.turbo-progress-bar {
  background-color: #ec005a;
}

#messages {
  border: 3px dashed red;
  border-radius: 5px;
  padding: 15px;
}

#messages > :first-child {
  margin-top: 0;
}

#messages > :last-child {
  margin-bottom: 0;
}

.inline-action {
  background-color: #ffe300;
  border-radius: 5px;
  padding: 5px 10px;
}

.inline-action--async {
  background-color: #00fff5;
}

